
*, *::before, *::after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: #368ba0;
}
#app {
  font: 14px/20px sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
#app header h1 {
    font-weight: 100;
    height: 80px;
    line-height: 80px;
    font-size: 38px;
}
#app footer {
    color: #555;
    margin-top: 60px;
}



