
.frame-wrapper {
  margin: 0 auto;
  position: relative;
  box-shadow: 0 0 0px 10px;
}
.frame-wrapper .original {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.frame-wrapper p.win {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    font-size: 40px;
    margin: 0 0;
    background: rgba(43, 181, 82, 0.7);
    text-transform: uppercase;
}
.frame {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background: #612211 url("../assets/board.jpg");
  background-size: cover;
}
.controls {
  margin-top: 30px;
}
.controls a {
    display: inline-block;
    text-decoration: none;
    padding: 6px 12px;
    background: #f78403;
    color: #fff;
    border-radius: 3px;
}
.controls a.toggle-original {
      background: #d05b88;
}
.controls a.restart {
      background: #368ba0;
}
.controls a.shuffle {
      background: #3ebb5c;
}



